function TranscriptView(props) {
  const thiz = props.context;
  var table = [];
  //table.push(<div className="transcript-renderer text-left">);
  const transcripts = props.context.state.transcript;
  var N = transcripts.length;
  
  for (let i = 0; i < N; i=i+1) {
    var transcript = transcripts[i];
    var future_time = thiz.state.transcript[Math.min(N-1,i+1)].time;
    var current_time = transcript.time;
    if(future_time > thiz.state.currentTimeAroll && current_time <= thiz.state.currentTimeAroll) {
      table.push(<div key={i} className="bg-primary text-white" onClick={props.context.clickTranscript(transcript.time)}><b>{transcript.time_readable}</b> {transcript.text}</div>);
      transcript_position = i;
    } else {
        var found_broll = false;
        for (let i = 0;i < added_broll_position.length && !found_broll; i=i+1) {
          if(added_broll_position[i] > current_time/1000 && added_broll_position[i] < future_time/1000) {
            found_broll = true;
          }
        }
        
        if (found_broll) {
          table.push(<div key={i} className="bg-warning" onClick={props.context.clickTranscript(transcript.time)}><b>{transcript.time_readable}</b> {transcript.text}</div>);
        } else {
          table.push(<div key={i} className="" onClick={props.context.clickTranscript(transcript.time)}><b>{transcript.time_readable}</b> {transcript.text}</div>);
        }
        
    }
  }
  return table
}

function TranscriptViewVeryIntelligent(props) {
  const thiz = props.context;
  var table = [];
  const words = props.context.state.words;
  var N = words.length;
  var pauses_pos = 0;
  var done_pauses = false;
  var counter_nonbreak = 0

  const added_broll_positions = props.context.state.added_broll_position
  const added_broll_durations = props.context.state.added_broll_duration
  const num_added_broll = added_broll_position.length;
  
  for (let i = 0; i < N; i=i+1) {
    var word = words[i];
    var future_time = words[Math.min(N-1,i+1)].start;
    var current_time = word.start;
    var text = word.word;
    var keywords = thiz.state.global_keywords;
    var is_kw = (keywords.indexOf(word.word) >= 0);
    var concepts = thiz.state.concepts;
    var this_concept = "";
    var currently_in_concept = false
    var concept_start = 0;
    for(let j=0;j<concepts.length;j++) {
      var start_concept = concepts[j][1];
      var end_concept = concepts[j][1] + 2;
      if (current_time > start_concept && current_time < end_concept) {
        concept_start = start_concept*1000;
        currently_in_concept = true;
        this_concept = concepts[j][0]
        break
      }
    }
    var broll_start = 0;
    var currently_in_broll = false;

    for(let j=0;j<num_added_broll;j++){
      var start_broll = added_broll_positions[j]
      var end_broll = start_broll + added_broll_duration[j]/1000
      console.log(current_time,start_broll,end_broll,"found broll in transcript")
      if (current_time > start_broll && current_time < end_broll) {
        broll_start = start_broll*1000;
        currently_in_broll = true;
        break
      }
    }

    if(future_time > thiz.state.currentTimeAroll/1000 && current_time <= thiz.state.currentTimeAroll/1000) {
      console.log(current_time,future_time,"called current time")
      table.push(<span key={i} className="bg-primary text-white text-transcript" onClick={props.context.clickTranscriptIntelligent(current_time*1000,i,text)}>{text}</span>);
      table.push(<span className="bg-primary text-white text-transcript">{" "}</span>);
    } else {
      if (currently_in_broll){
        table.push(<span key={i} className="bg-success text-transcript" data-tip={this_concept} onClick={props.context.clickTranscriptIntelligent(broll_start-200,i,this_concept)}>{text}</span>);
        table.push(<span className="bg-success text-transcript">{" "}</span>);
      } else if (currently_in_concept){
        table.push(<span key={i} className="bg-info text-transcript" data-tip={this_concept} onClick={props.context.clickTranscriptIntelligent(concept_start,i,this_concept)}>{text}</span>);
        table.push(<span className="bg-info text-transcript">{" "}</span>);
      } else {
        table.push(<span key={i} className="text-transcript" onClick={props.context.clickTranscriptIntelligent(current_time*1000,i,text)}>{text}</span>);
      table.push(<span>{" "}</span>);
      }
    }
    if (word.word == "."){
      if (counter_nonbreak > 15) {
        table.push(<span><br/></span>);
        counter_nonbreak = 0
      }
    }
    counter_nonbreak += 1;
  }
  return table
}


function TranscriptViewIntelligent(props) {
  const thiz = props.context;
  var table = [];
  //table.push(<div className="transcript-renderer text-left">);
  const transcripts = props.context.state.transcript;
  var N = transcripts.length;
  var pauses_pos = 0;
  var done_pauses = false;
  
  for (let i = 0; i < N; i=i+1) {
    var transcript = transcripts[i];
    var future_time = thiz.state.transcript[Math.min(N-1,i+1)].time;
    var current_time = transcript.time;
    var utterance = transcript.text;
    utterance = utterance.split(" ");
    var keywords = thiz.state.suggestions[i];
    var utteranceIntelligent = "";
    var keyword_highlighted = ""
    for (let j = 0;j < utterance.length;j++) {
      if (thiz.state.global_keywords.includes(utterance[j])) {
        keyword_highlighted = utterance[j];
        utterance[j] = "<a style='color: blue;'>"+utterance[j]+"</a>"
        break
      }
      if (thiz.state.global_keywords_sentiment.includes(utterance[j])) {
        keyword_highlighted = utterance[j];
        utterance[j] = "<a style='color: orange;'>"+utterance[j]+"</a>"
        break
      }
    }
    utterance = utterance.join(" ")
    var has_aro = false;
    for(let h = 0;h<thiz.state.arousal.length;h++){
      if (future_time > thiz.state.arousal[h]*1000 && current_time <= thiz.state.arousal[h]*1000) {
        has_aro = true;
      }
    }
    if (has_aro) {
      utterance = "<u>" + utterance + "</u>"
    } else {
      //utterance = "<b class='text-danger'>   </b>" + utterance
    }
    //check if there is a current possibility for broll
    var this_broll = false
    for(let k = 0;k<thiz.state.pauses.length;k++) {
      if (future_time > thiz.state.pauses[k]*1000 && current_time <= thiz.state.pauses[k]*1000) {
        this_broll = true;
        break;
      }
    }

    console.log(current_time,future_time,this_broll)
    if(future_time > thiz.state.currentTimeAroll && current_time <= thiz.state.currentTimeAroll) {
      if (!this_broll){
        table.push(<div key={i} className="bg-primary text-white" onClick={props.context.clickTranscriptIntelligent(transcript.time,i,keyword_highlighted)}><b>{transcript.time_readable}</b> <span dangerouslySetInnerHTML={{__html: utterance}}></span></div>);
      } else {
        table.push(<div key={i} className="bg-primary text-white" onClick={props.context.clickTranscriptIntelligent(transcript.time,i,keyword_highlighted)}><b className="bg-secondary text-white">{transcript.time_readable}</b> <span dangerouslySetInnerHTML={{__html: utterance}}></span></div>);
      }
      transcript_position = i;
    } else {
        var found_broll = false;
        for (let i = 0;i < added_broll_position.length && !found_broll; i=i+1) {
          if(added_broll_position[i] > current_time/1000 && added_broll_position[i] < future_time/1000) {
            found_broll = true;
          }
        }
        
        if (found_broll) {
          table.push(<div key={i} className="bg-warning" onClick={props.context.clickTranscriptIntelligent(transcript.time,i,keyword_highlighted)}><b>{transcript.time_readable}</b> <span dangerouslySetInnerHTML={{__html: utterance}}></span></div>);
        } else {
          if (!this_broll){
          table.push(<div key={i} className="" onClick={props.context.clickTranscriptIntelligent(transcript.time,i,keyword_highlighted)}><b>{transcript.time_readable}</b> <span dangerouslySetInnerHTML={{__html: utterance}}></span></div>);
          } else {
          table.push(<div key={i} className="" onClick={props.context.clickTranscriptIntelligent(transcript.time,i,keyword_highlighted)}><b className="bg-secondary text-white">{transcript.time_readable}</b> <span dangerouslySetInnerHTML={{__html: utterance}}></span></div>);
          }
        }
        
    }
  }
  return table
}

