function RenderBrollResult(props) {
	const thiz = props.context;
    var table = []
    const broll_footage = thiz.state.broll_footage;
    const broll_thumbnail = thiz.state.broll_thumbnail;
    const N = broll_footage.length;
    const per_row = 4;
    // Outer loop to create parent
    for (let i = 0; i < N; i+=per_row) {
      var children = []
      //Inner loop to create children
      for (let j = 0; j < per_row; j++) {
        children.push(
		    <div className="col-3">
				<video width="260" height="250" key={broll_footage[i+j]} name="broll" controls>
					<source src={broll_footage[i + j]} type="video/mp4"/>
					Your browser does not support the video tag.
				</video>
				<button onClick={thiz.addNow(broll_footage[i + j],broll_thumbnail[i+j],i+j)} className="btn btn-primary">Add Now</button>
		    </div>
        	)
      }
      //Create the parent and add the children
      table.push(<div key={i} className="row top-buffer">{children}</div>)
    }
    return table
  }

  function RenderBrollResultIntelligent(props) {
    const thiz = props.context;
    const start = props.start;
    var table1 = []
    var broll_footage = thiz.state.broll_footage;
    var broll_thumbnail = thiz.state.broll_thumbnail;
    var N = broll_footage.length;
    if (N < 24) {
      return table1
    }
    var per_row = 2;
    // Outer loop to creaSearte parent
    var counter = 0;
    for (let i = start; i < start+12; i+=per_row) {
      var children = []
      //Inner loop to create children
      counter += 1;
      for (let j = 0; j < per_row; j++) {
        children.push(
        <div className="col-6">
          <video width="200" height="200" key={broll_footage[i+j]} name="broll" controls autoPlay loop muted>
            <source src={broll_footage[i + j]} type="video/mp4"/>
            Your browser does not support the video tag.
          </video>
          <button onClick={thiz.addNow(broll_footage[i + j],broll_thumbnail[i+j],i+j)} className="btn btn-primary">Add Now</button>
        </div>
          )
      }
      //Create the parent and add the children
      table1.push(<div key={i} className="row top-buffer">{children}</div>)
    }
    return table1
  }

