function dragMoveListener (event) {
  var target = event.target,
      // keep the dragged position in the data-x/data-y attributes
      x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx;
  var broll_id = parseInt(target.getAttribute('broll-id'));
  var new_relative_position = parseFloat(target.getAttribute('data-x'))/800;
  console.log(parseFloat(target.getAttribute('data-x')));
  //console.log(target.getAttribute('data-x'));
  console.log("________",new_relative_position,new_relative_position * document.getElementById("aroll").duration)
  added_broll_position[broll_id] = new_relative_position * document.getElementById("aroll").duration;
  // translate the element
  target.style.webkitTransform =
  target.style.transform =
    'translate(' + x + 'px, ' + 0 + 'px)';

  // update the posiion attributes
  target.setAttribute('data-x', x);
}

var ok_to_delete = true;

function dragMoveListenerControl (event) {
  var target = event.target,
      // keep the dragged position in the data-x/data-y attributes
      x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx;
  // translate the element
  var progress_fraction = (parseFloat(target.getAttribute('data-x')))/800;
  if (progress_fraction > 1){
    progress_fraction = 1
  }
  if (progress_fraction < 0) {
    progress_fraction = 0
  }
  console.log("progress_fraction: " + progress_fraction)
  //console.log((parseFloat(target.getAttribute('data-x')))/800);
  target.style.webkitTransform =
  target.style.transform = 'translate(' + x + 'px, 0px)';

  // update the posiion attributes
  target.setAttribute('data-x', x);

  // Update the current slider value (each time you drag the slider handle)
  document.getElementById("aroll").pause();
  document.getElementById("aroll").currentTime = (document.getElementById("aroll").duration*progress_fraction);
}

