      transcript: [{time: 0,time_readable: "00:00",text: "it is May 31st so this is just like"},{time: 3659,time_readable: "00:03",text: "quick little video about something that"},{time: 5850,time_readable: "00:05",text: "happened earlier this week one of my cat"},{time: 8580,time_readable: "00:08",text: "Olaf went missing and he is just sitting"},{time: 10650,time_readable: "00:10",text: "over there so what happened basically"},{time: 15330,time_readable: "00:15",text: "the weeks prior is I've had my balcony"},{time: 17760,time_readable: "00:17",text: "door open I'm on the third floor of an"},{time: 20430,time_readable: "00:20",text: "apartment so I'm not really worried"},{time: 22020,time_readable: "00:22",text: "about something like jumping in for the"},{time: 24090,time_readable: "00:24",text: "felony but so yeah I had that door open"},{time: 28109,time_readable: "00:28",text: "for weeks on end because it's just been"},{time: 31070,time_readable: "00:31",text: "like really getting really hot we have"},{time: 33989,time_readable: "00:33",text: "like really severe winters and then we"},{time: 36360,time_readable: "00:36",text: "have really brutal summers as well which"},{time: 39030,time_readable: "00:39",text: "is kind of a weird weather here so like"},{time: 44219,time_readable: "00:44",text: "I wasn't getting too much bugs so I just"},{time: 46829,time_readable: "00:46",text: "decided I'll leave the door open and"},{time: 48989,time_readable: "00:48",text: "they can go in and out freely as much as"},{time: 52050,time_readable: "00:52",text: "they want I kind of liked letting the"},{time: 54390,time_readable: "00:54",text: "two cats have their freedom and so yeah"},{time: 59070,time_readable: "00:59",text: "I used to shut the screen door at night"},{time: 62910,time_readable: "01:02",text: "and bring them in but because they would"},{time: 65729,time_readable: "01:05",text: "always go out late at night I was like"},{time: 67380,time_readable: "01:07",text: "okay I'll just let them stay out all"},{time: 69570,time_readable: "01:09",text: "night and then it turned into the thing"},{time: 71280,time_readable: "01:11",text: "where I left the door open for weeks on"},{time: 73049,time_readable: "01:13",text: "end at a time so it was this Monday I"},{time: 76229,time_readable: "01:16",text: "believe where I woke up and my count"},{time: 79439,time_readable: "01:19",text: "Hans was calling me to wake me up but"},{time: 83070,time_readable: "01:23",text: "Olaf wasn't buying me a wake-up which at"},{time: 86430,time_readable: "01:26",text: "first I thought okay that's strange"},{time: 88409,time_readable: "01:28",text: "because usually they tagged him and"},{time: 90659,time_readable: "01:30",text: "usually Olaf's the first one to wake me"},{time: 92579,time_readable: "01:32",text: "up you know it stretches his paw my face"},{time: 94799,time_readable: "01:34",text: "and lightly presses his claws on me to"},{time: 97650,time_readable: "01:37",text: "try to wake me up so I thought okay and"},{time: 103229,time_readable: "01:43",text: "I didn't think anything of it so I went"},{time: 105180,time_readable: "01:45",text: "to the kitchen and I fed Hans and I"},{time: 108479,time_readable: "01:48",text: "didn't call for Olaf I thought as soon"},{time: 110250,time_readable: "01:50",text: "as the food was out he would hear then I"},{time: 112590,time_readable: "01:52",text: "went back to sleep for a bit and I saw"},{time: 114750,time_readable: "01:54",text: "okay this is strange I still haven't"},{time: 117420,time_readable: "01:57",text: "seen a lot so I got back up and I went"},{time: 120540,time_readable: "02:00",text: "to the balcony and to my surprise he"},{time: 123360,time_readable: "02:03",text: "wasn't outside on the balcony I"},{time: 125670,time_readable: "02:05",text: "immediately went into like a panic"},{time: 128449,time_readable: "02:08",text: "and I already have anxiety and I I don't"},{time: 131750,time_readable: "02:11",text: "know what it's like to lose a real human"},{time: 134090,time_readable: "02:14",text: "kid but when cats are all you have it's"},{time: 137930,time_readable: "02:17",text: "like losing a child you know it's not"},{time: 139849,time_readable: "02:19",text: "the same but you know you know what I"},{time: 141349,time_readable: "02:21",text: "mean like when you lose a pet that you"},{time: 143360,time_readable: "02:23",text: "care for every day it is like losing"},{time: 146120,time_readable: "02:26",text: "someone so my cat was missing so I"},{time: 150910,time_readable: "02:30",text: "panicked and I said Hans where's Olaf"},{time: 153830,time_readable: "02:33",text: "and Hans was just looking at me like he"},{time: 156800,time_readable: "02:36",text: "didn't know so I went to my closet and I"},{time: 159680,time_readable: "02:39",text: "was carrying through all my papers and"},{time: 162140,time_readable: "02:42",text: "then I found that 24-hour pet watch or"},{time: 166250,time_readable: "02:46",text: "something like that it's called and I"},{time: 169400,time_readable: "02:49",text: "called them and I gave them the"},{time: 171170,time_readable: "02:51",text: "microchip number and they kept like"},{time: 173569,time_readable: "02:53",text: "dragging on the conversation and asked"},{time: 176540,time_readable: "02:56",text: "me like what year was he born and I was"},{time: 178250,time_readable: "02:58",text: "like I don't know but I know"},{time: 179750,time_readable: "02:59",text: "approximately how old he is"},{time: 181610,time_readable: "03:01",text: "cuz when I adopted him from the Humane"},{time: 183920,time_readable: "03:03",text: "Society he was like 7 or 8 and that was"}],
